import React, {Component} from 'react';
import {hashHistory} from 'react-router'
import './management.css';
import {get520OrderList} from '../../services/api';
import {weChatPay} from '../../components/WechatPay/wechatPay'

export default class Management extends Component {
    constructor(props) {
        super(props);
        this.state = {
            orderList: [],
            payStatus : ['订单取消','待支付','支付成功']
        }
    }

    componentDidMount() {
        get520OrderList(this.props.location.query).then((data) => {
            this.setState({
                orderList: data.data
            })
        })
    }

    toFiveTwoZero = () => {
        hashHistory.push('/520')
    }

    toPay=(item)=>{
        console.log(item);
        weChatPay({
            "orderId": item.orderId,
            "payFee": item.payFee,
            "body": item.body,
            "payType": "MWEB",
            "businessType": 'activity',
            "attach": JSON.stringify({'attach':'activity'})
        },'yxcat.com.cn/ddp-activities/#management?activityId=20180520&mobile='+this.state.mobile)
    }

    render() {
        return (
            <div>
                <div
                    className="backgroundOrange managementT"
                    onClick={this.toManagement}>
                    {/*<img onClick={this.toFiveTwoZero}*/}
                         {/*className="backArrow" alt="<"*/}
                         {/*src="assets/img/back>.png"/>*/}
                         <div></div>
                    <div className="tiltle520_second">
                        订单管理
                    </div>
                    <a href='tel:18717717190'>
                        <img alt="back>"
                             src="assets/img/kefu.png"/>
                    </a>
                </div>
                <div className="cardContain_management">
                    {
                        this.state.orderList && this.state.orderList.map((item, key) => {
                            return (
                                <div
                                    className="managementCard"
                                    key={key}>
                                    <div
                                        className="manageTitle">
                                        订单编号：{item.orderNo}
                                        <span>{this.state.payStatus[item.payStatus+1]}</span>
                                    </div>
                                    <div
                                        className="manageBody">
                                        <div>
                                            商品名称：{item.productName}</div>
                                        <div>
                                            下单时间：{item.orderTime}</div>
                                        <div>
                                            卡密：{item.consignee}</div>
                                        <div>
                                            金额：{item.payPrice}</div>
                                        <div>
                                            备注：若支付成功，券码详情将通过短信发送至您手机。</div>
                                    </div>
                                    {item.payStatus===0?(<div
                                        className="manageBtn">
                                        <button
                                            className="payBtn" onClick={(item)=>this.toPay(item)}>
                                            付款
                                        </button>
                                    </div>) : ('')}
                                </div>
                            )
                        })
                    }
                </div>
            </div>
        )
    }
}


